<template>
  <div id="app">
    <div style="margin:10px 0">element-table表头筛选demo</div>
    <el-button style="margin-bottom:10px;" type="primary" size="mini" @click="addRow">新增</el-button>
    <el-table
      ref="table"
      :data="tableData"
      border
      size="mini"
      highlight-current-row
    >
      <el-table-column
        align="center"
        label="id"
        prop="userId"
        width="100"
        show-overflow-tooltip
      />
      <el-table-column
        align="center"
        label="姓名"
        prop="userName"
        width="150"
        show-overflow-tooltip
      >
        <template slot="header" slot-scope="scope">
          <el-table-header-filter
            :data="tableData"
            label="姓名"
            :filterParams="{
              label: 'userName',
              prop: 'userId',
            }"
            @submit="handleSelectUser"
          ></el-table-header-filter>
        </template>
      </el-table-column>

      <el-table-column
        align="center"
        label="公司"
        width="150"
        prop="companyName"
        show-overflow-tooltip
      >
        <template slot="header" slot-scope="scope">
          <el-table-header-filter
            :data="tableData"
            label="公司"
            :filterParams="{
              label: 'companyName',
              prop: 'companyId',
            }"
            width="150"
            :isCheckbox="false"
            @submit="handleSelectCompany"
          ></el-table-header-filter>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="部门"
        width="150"
        prop="deptName"
        show-overflow-tooltip
      >
        <template slot="header" slot-scope="scope">
          <el-table-header-filter
            :data="tableData"
            label="部门"
            :filterParams="{
              label: 'deptName',
              prop: 'deptId',
            }"
            width="150"
            :isCheckbox="true"
            @submit="handleSelectDept"
          ></el-table-header-filter>
        </template>
      </el-table-column>

      <el-table-column
        align="center"
        label="年龄"
        width="80"
        prop="year"
        show-overflow-tooltip
      />
      <el-table-column
        align="center"
        label="性别"
        width="80"
        prop="sex"
        show-overflow-tooltip
      />
    </el-table>

    <el-button
      style="margin-top:10px;"
      type="primary"
      size="mini"
      @click="changeValue"
      >修改李四姓名</el-button
    >
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      tableList: [
        {
          userId: '1',
          userName: '张三',
          companyName: '小麦有限公司',
          companyId: 'A0001',
          deptName: '软件一部',
          deptId: 'D001',
          year: 30,
          sex: '男',
        },
        {
          userId: '2',
          userName: '李四',
          companyName: '阿里豆豆有限公司',
          companyId: 'A0002',
          deptName: '软件二部',
          deptId: 'D002',
          year: 38,
          sex: '男',
        },
        {
          userId: '3',
          userName: '王五',
          companyName: '企鹅有限公司',
          companyId: 'A0003',
          deptName: '软件三部',
          deptId: 'D003',
          year: 22,
          sex: '女',
        },
        {
          userId: '4',
          userName: '小明',
          companyName: '企鹅有限公司',
          companyId: 'A0003',
          deptName: '软件二部',
          deptId: 'D002',
          year: 40,
          sex: '女',
        },
        {
          userId: '5',
          userName: '小方',
          companyName: '大米有限公司',
          companyId: 'A0004',
          deptName: '市场部',
          deptId: 'D020',
          year: 40,
          sex: '男',
        },
        {
          userId: '6',
          userName: '小乐',
          companyName: '大米有限公司',
          companyId: 'A0004',
          deptName: '市场部',
          deptId: 'D020',
          year: 40,
          sex: '男',
        },
      ], //表数据

      addList: [
        {
          userId: '7',
          userName: '小天',
          companyName: '心心念念有限公司',
          companyId: 'A0005',
          deptName: '测试部',
          deptId: 'D021',
          year: 40,
          sex: '男',
        },
        {
          userId: '99999',
          userName: 'god',
          companyName: 'XXYY有限公司',
          companyId: 'A9999',
          deptName: '划水部',
          deptId: 'X99',
          year: 1000,
          sex: '未知',
        },
      ],
      companyFilter: [], //保存筛选公司id
      userNameFilter: [], //保存筛选姓名id
      deptFilter: [], //保存筛选部门id
    }
  },

  computed: {
    tableData() {
      return this.tableList.filter((data) => {
        return (
          (this.companyFilter.length === 0 ||
            this.companyFilter.includes(data.companyId)) &&
          (this.userNameFilter.length === 0 ||
            this.userNameFilter.includes(data.userId)) &&
          (this.deptFilter.length === 0 ||
            this.deptFilter.includes(data.deptId))
        )
      })
    },
  },

  methods: {
    //表头过滤选择公司
    handleSelectCompany(val) {
      if (val && val.length === 0) {
        this.companyFilter = []
        return
      }
      this.companyFilter = val && val.companyId ? [val.companyId] : []
    },
    //表头过滤选择姓名
    handleSelectUser(val) {
      if (val && val.length === 0) {
        this.userNameFilter = []
        return
      }
      this.userNameFilter = val && val.userId ? [val.userId] : []
    },
    // 表头过滤选择部门
    handleSelectDept(val) {
      if (val && val.length === 0) {
        this.deptFilter = []
        return
      }
      const arr = []
      val.map((item) => {
        arr.push(item.deptId)
      })
      this.deptFilter = arr
    },
    changeValue() {
      this.tableList[1].userName = 'a先生'
      this.tableList[1].userId = '44'
      this.$set(this.tableList, 1, this.tableList[1])
    },

    addRow() {
      if (this.addList.length === 0) {
        this.$message.warning('别加了，就给了两条预存数据 o(╥﹏╥)o')
        return false
      }
      this.tableList.push(this.addList[0])
      this.addList.shift()
    },
  },
}
</script>



